import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-admin-record-list',
  templateUrl: './admin-record-list.component.html',
  styleUrls: ['./admin-record-list.component.scss']
})
export class AdminRecordListComponent implements OnInit {

  routeSub: Subscription;
  currentTable = null;

  constructor(
    private activatedRoute: ActivatedRoute
  ) { }

  ngOnInit(): void {
    this.routeSub = this.activatedRoute.params.subscribe( params => {
      this.currentTable = params.table;
    })
  }

}
